




























































































































import { Component, Vue } from 'vue-property-decorator'
import { readOperationsCustomers, readOperationsOneCustomer } from '../../../store/operations/getters'
import { dispatchGetCustomers, dispatchGetCustomerByEmail } from '../../../store/operations/actions'
import ResizableView from '../../../components/utility/ResizableView.vue'
import { ICustomerSearch } from '../../../interfaces/index'
import ResultCard from './ResultCard.vue'

@Component({
  components: {
    ResizableView,
    ResultCard,
  },
})
export default class Customers extends Vue {
  public headers = [
    {
      text: 'First Name',
      sortable: true,
      value: 'firstName',
      align: 'left',
    },
    {
      text: 'Last Name',
      sortable: true,
      value: 'lastName',
      align: 'left',
    },
    {
      text: 'Email',
      sortable: true,
      value: 'email',
      align: 'left',
    },
    {
      text: 'SSN4',
      sortable: true,
      value: 'ssn4',
      align: 'left',
    },
    {
      text: 'DOB',
      sortable: true,
      value: 'dob',
      align: 'left',
    },
    {
      text: 'Phone Number',
      sortable: true,
      value: 'phone_number',
      align: 'left',
    },
    {
      text: 'Home Address',
      sortable: true,
      value: 'home_address',
      align: 'left',
    },
  ]

  public customerEmail: string = ''
  public firstNameParam: string = ''
  public lastNameParam: string = ''
  public ssn4Param: string = ''
  public dobParam: string = ''
  public params: ICustomerSearch = {}
  public showAdvancedOptions: boolean = false
  public searchedFields: ICustomerSearch = {}

  get customers() {
    return readOperationsCustomers(this.$store)
  }

  get oneCustomer() {
    return readOperationsOneCustomer(this.$store)
  }

  public async mounted() {
    await dispatchGetCustomers(this.$store, this.params)
  }

  public clearInput(field) {
    if (field === 'email') {
      this.customerEmail = ''
    } else {
      this.firstNameParam = ''
      this.lastNameParam = ''
      this.ssn4Param = ''
      this.dobParam = ''
    }
  }

  public toggleSearchOptions() {
    this.showAdvancedOptions = !this.showAdvancedOptions
  }

  public async searchCustomer() {
    if (this.customerEmail) {
      await dispatchGetCustomerByEmail(this.$store, this.customerEmail)
    } else {
      if (this.firstNameParam?.length) {
        this.params.firstName = this.firstNameParam
      }
      if (this.lastNameParam?.length) {
        this.params.lastName = this.lastNameParam
      }
      if (this.ssn4Param?.length) {
        this.params.ssnLast4 = Number(this.ssn4Param)
      }
      if (this.dobParam?.length) {
        this.params.dob = this.dobParam
      }
      console.log('params after build: ', this.params)
      await dispatchGetCustomers(this.$store, this.params)
      // reset params after search
      this.searchedFields = this.params
      this.params = {}
    }
  }

  public consolelog(name) {
    console.log(name)
  }
}
