






























import { Prop } from 'vue-property-decorator'
import { dispatchSetCustomer } from '../../../store/operations/actions'
import { dispatchGetCustomerActivities } from '../../../store/operations/actions'
import { Component, Vue } from 'vue-property-decorator'
import { ICustomerProfile } from '../../../interfaces'

@Component({})
export default class ResultCard extends Vue {

  @Prop(Object) public customer!: ICustomerProfile
  public searchByEmail = ''
  public searchedFields = {}

  public selectCustomer() {
    dispatchSetCustomer(this.$store, this.customer)
    if (this.customer.id) {
      dispatchGetCustomerActivities(this.$store, this.customer.id)
    }
  }

  public consolelog() {
    console.log(this.searchByEmail)
    console.log(this.searchedFields)
  }
}
